import { layoutDataPipeline } from './pipeline';
import { LayoutDataPipelineOptions } from '../interfaces';

export { layoutDataPipeline } from './pipeline';

export const getLayoutData = async (options: LayoutDataPipelineOptions) => {
    console.log('get layout data', options.routeParams);
    return layoutDataPipeline
        .run({
            ...options,
            layoutData: null,
        })
        .then((context) => context.layoutData);
};
